import loginSheet from "../../components/loginSheet";
export default {
	data(){
		return {
			userInfo:null,
			showSheet:false,
		}
	},
	onLoad(){
		this.checkAuth();
	},
	components:{
		loginSheet
	},
	methods:{
		async checkAuth() {
		  let that = this;
		  this.userInfo = uni.getStorageSync("user_info") || null;
		  if (!this.userInfo) {
		    that.showSheet = true;
		  }
		},
		authSuccess(e) {
		  this.userInfo = uni.getStorageSync("user_info") || null;
		  this.showSheet = false;
		},
		goPage(path){
			uni.navigateTo({
			  url: path
			});
		}
	}
};
